@charset "utf-8";

body {
  font-family: "Raleway", "inter", sans-serif;
  background: var(--clr-bg) url('../Media/vintage-map.jpg') no-repeat center center;
  background-size: cover;
  background-attachment: fixed;

}
main, footer {
  position: relative;
  position: relative;
  z-index: 5000;
}
main {
  display: block;
  margin: 0;
  padding: 5px;
  margin-bottom: 50px;
  border-radius: var(--app-border-rad);
}
a {
  text-decoration: none;
  color: inherit;
  
}
/*----------------------------------------------------------->
  Utility Functions
<-----------------------------------------------------------*/
.container {
  width: min(100% - 30px, 1200px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.f-col {
  display: flex;
  flex-direction: column;
}

.hidden {
  display: none;
  opacity: 0;
}

.circle {
  border-radius: 50%;
}
.align-center {
  align-items: center;
}
.center {
  justify-content: center;
  align-items: center;
}
.mt-75 {
  margin-top: 50px;
}
.mb-10 {
  margin-bottom: 10px;
}
.w-100 {
  width: 100%;
}
.h-100 {
  height: 100%;
}
.h-150px {
  height: 150px;
}
.w-300px{
  width: 300px;
}
.flex-end{
  align-self: flex-end;
}

.space-between {
  justify-content: space-between;
}
.pos-rel {
  position: relative;
  z-index: 1;
}

.no-select {
  -webkit-user-select: none; /* Chrome, Safari */
  -moz-user-select: none;    /* Firefox */
  -ms-user-select: none;     /* Internet Explorer/Edge */
  user-select: none;         /* Standard */
}
.blur-bg {
  background-color: #D7CCC855;
  backdrop-filter: blur(3px);
  border-radius: var(--app-border-rad);
}
.underline{
  text-decoration: underline;
  margin-top: 10px;
  transition: var(--app-transition);
}
.underline:hover {
  background-color: var(--clr-bg);
}
.pointer {
  cursor: pointer;
}
/*----------------------------------------------------------->
  Style Specific
<-----------------------------------------------------------*/
.orange-text {
  color: var(--clr-primary);
}

.small-title {
  font-weight: 600;
  font-size: 1.6rem;
  margin-top: var(--app-spacing);
}

@media(max-width: 800px) {
  .small-title {
    font-size: 1.4rem;
  }
}
@media(max-width: 500px){
  .small-title{
    font-size: 1.2rem;
  }
}

/* ---- Mobile Visibility .small-shide----*/
@media(max-width: 900px) {
  .small-hide {
    display: none;
    opacity: 0;
  }
}

/*----------------------------------------------------------->
  Buttons
<-----------------------------------------------------------*/
.btn {
  height: 40px;
  line-height: 40px;
  width: fit-content;
  align-items: center;
  background-color: var(--clr-drk);
  border: 1px solid transparent;
  border-radius: var(--app-border-rad);
  padding: 0 10px;
  color: var(--clr-light);
  font-size: var(--font-size-reg);
  font-weight: 600;
  justify-content: center;
  display: flex;
  position: relative;
  transition: var(--app-transition);
  cursor: pointer;
}

.btn.secondary {
  background-color: var(--clr-light);
  color: var(--clr-drk);
  border: solid 2px var(--clr-drk);
}
.btn.small {
  height: 20px;
  line-height: 20px;
  font-size: var(--font-size-small);
}

.btn:hover {
  filter: brightness(1.3);
}

.btn:active {
  background-color: #c85000;
}

.btn.secondary:hover{
  filter: brightness(1.8);
}

/*------------------------------------------------------------->
  Framer Motion Transitions
<-------------------------------------------------------------*/
.fade-enter {
  opacity: 0;
  transform: translateY(10px);
}
.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms ease, transform 300ms ease;
}
.fade-exit {
  opacity: 1;
  transform: translateY(0);
}
.fade-exit-active {
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 300ms ease, transform 300ms ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}
.product-card {
  border: 1px solid #ccc;
  border-radius: var(--app-border-rad);
  padding: 1rem;
  max-width: 250px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  position: relative;
  transition: var(--app-transition);
  background-color: var(--clr-light);
  box-shadow: 1px 1px 7px rgba(0,0,0,0.15);

}
.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 2px 2px 10px rgba(0,0,0,0.25);
}
.product-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.product-card a {
  color: #007bff;
  text-decoration: none;
}
.product-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--app-spacing) 0;
  margin: var(--app-spacing) 0;
}
.product-display h2 {
  font-size: var(--font-size-subtitle);
  color: var(--clr-light);
    text-shadow:
    -1px -1px 0 var(--clr-drk),
    1px -1px 0 var(--clr-drk),
    -1px  1px 0 var(--clr-drk),
    1px  1px 0 var(--clr-drk);
}
.product-grid {
  width: 100%;
  padding: var(--app-spacing);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}
.pagination {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}

.details-section{
  height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.details-card {
  display: flex;
  flex-direction: column;
  background-color: var(--clr-light);
  max-width: 500px;
  border-radius: var(--app-border-rad);
  padding: var(--app-spacing);  
}
.details-card h2 {
  font-size: var(--font-size-subtitle);
  color: #000;
  text-align: center;
}

.details-card p {
  margin: 10px 0;
}
.filter-menu {
  width: 500px;
  height: 50px;
  border-radius: var(--app-border-rad);
  cursor: pointer;
  font-size: var(--font-size-reg);
}
.search-bar {
  width: 300px;
}
.search-bar, .input-field {
  height: 40px;
  border-radius: var(--app-border-rad);
  padding-left: 10px;
  background-color: var(--clr-primary);
}
.search-form {
  display: flex;
  width: 500px;
  justify-content: space-between;
}
/* Add this to your styles for sales products */
.sales-card {
  position: relative;
}

.sales-card::before {
  content: "Sale!";
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #ff4d4d;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-weight: bold;
  font-size: 0.8em;
  text-transform: uppercase;
}

.favorite-btn {
  position: absolute;
  top: 10px;
  left: 10px;
  border: none;
  background-color: var(--clr-light);
  font-size: var(--font-size-reg);
  transition: var(--app-transition);
  cursor: pointer;
}
.favorite-btn:hover {
  transform: scale(1.1);
  backdrop-filter: brightness(0.8);
}

.quantity-adjuster span {
  font-size: var(--font-size-reg);
  color: var(--clr-drk);
}

.banner-section {
  margin: var(--app-spacing) 0;
  background-color: var(--clr-bg);;
}
.banner-section img {
  cursor: pointer;
}